.price-general {
    &__header {
        display: block;
    }
    &__container {
        overflow: hidden;
        @media (max-width: 600px){
            flex-shrink: 0; 
            width: 570px;
        }
    }
    &__wrapper {
    }
    &__item {
    }
}
.price-card {
    box-sizing: border-box;
    padding: 30px;
    padding-top: 74px;
    height: 270px;
    border-radius: 20px;
    background: var(--green2);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    position: relative;
    &:hover{
        background: var(--darkgreen);
    }
    &__link {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
    &__area {
        color: var(--light-color);
        font-size: 36px;
        font-weight: 600;
        line-height: 130%; /* 46.8px */
    }
    &__price {
        width: 100%;
        padding: 12px;
        z-index: 10;
        border-radius: 10px;
    }
}
