.dark-card-small {
    height: 110px;
    border-radius: 20px;
    padding: 25px 30px;
    background: var(--brown);
    color: var(--light-color);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    img{
        height: 60px;
        width: 60px;
        margin-right: 10px;
    }
    &__title {
        max-width: 67%;
        color: var(--light-color);
        font-size: 16px;
        font-weight: 500;
        line-height: 130%; /* 20.8px */
        small{
            font-size: 14px;
        }
    }
    &__add-info{
        color: #00FF23;
        font-size: 24px;
        font-weight: 500;
        line-height: 90%;
        small{
            font-size: 12px;
        }
    }
    @media (max-width: 500px){
        gap: 20px;
        padding: 15px;
        img{
            height: 50px;
            width: 50px;
        } 
        &__title{
            font-size: 14px;
        }
        &__add-info{
            font-size: 18px;
        }

    }
}
.dark-card {
    box-sizing: border-box;
    border-radius: 20px;
    background: var(--brown);
    color: var(--light-color);
    padding: 26px 68px 30px 30px;
    height: 216px;
    @media (max-width: 1440px){
        padding-right: 30px;
        height: 250px; 
    }
    @media (max-width: 760px){
        padding: 26px;
        min-width: 280px;
        height: 280px; 
    }
    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        @media (max-width: 1100px){
             margin-bottom: 20px; 
        }
    }
    &__avatar {
        margin-right: 20px;
        img{
            width: 64px;
            height: 64px;
            border-radius: 64px;
            object-fit: cover;
        }
    }
    &__title {
        font-size: 16px;
        font-weight: 700;
        line-height: 130%; /* 20.8px */
    }
    &__text {
        font-size: 20px;
        font-weight: 500;
        line-height: 130%; /* 26px */
        @media (max-width: 1240px){
             font-size: 18px; 
        }
        @media (max-width: 970px){
             font-size: 16px; 
        }
    }
}
