
.benefits {
    &__title {
        max-width: 540px;
    }
    &__wrapper {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
        @media (max-width: 970px){
             grid-template-columns: repeat(2, 1fr); 
             grid-template-rows: repeat(2, 1fr); 
        }
        @media (max-width: 500px){
             grid-template-columns: repeat(1, 1fr); 
             grid-template-rows: repeat(4, 1fr); 
        }
    }
    &__item {
        padding: 38px 10px;
        border-radius: 20px;
        background: #D4D4D4;
        text-align: center;
    }
    &__item-image {
        margin-bottom: 44px;
        height: 144px;
        @media (max-width: 1100px){
             height: 120px;
             margin-bottom: 34px; 
        }
        @media (max-width: 760px){
             height: 100px;
        }
        @media (max-width: 500px){
             height: 75px;
             margin-bottom: 30px; 
        }
    }
    &__item-title {
        color: #000;
        font-size: 24px;
        font-weight: 500;
        line-height: 130%; /* 31.2px */
        @media (max-width: 1100px){
             font-size: 20px; 
        }
        @media (max-width: 500px){
             font-size: 16px; 
        }
    }
}
