.header {
    position: fixed;
    z-index: 10;
    padding: 20px 0;
    background: radial-gradient(1942.80% 462.32% at 26.53% -112.14%, #1C4D2D 0%, #00FF4E 100%);
    color: var(--light-color);
    min-width: 100%;
    @media (max-width: 1100px){
         padding: 14px 0; 
    }
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        img{
            width: 236px;
            height: 59px;
            @media (max-width: 1440px){
                 width: 200px;
                 height: 50px; 
            }
            @media (max-width: 1240px){
                 width: 164px;
                 height: 41px; 
            }
        }
    }
    &__nav {
        display: flex;
        align-items: center;
        column-gap: 40px;
        font-size: 20px;
        font-weight: 600;
        @media (max-width: 1650px){
            column-gap: 30px; 
            font-size: 18px;
       }
       @media (max-width: 1440px){
            column-gap: 20px;
            font-size: 16px;
       }
       @media (max-width: 1240px){
            font-size: 14px;
       }
       @media (max-width: 1100px){
            font-size: 12px;
       }
    }
    &__nav-tel {
        color: var(--light-color);
        font-weight: 700;
    }
    &__nav-btn {
        padding: 16px 26px;
        border-radius: 30px;
        background-color: #112E1B;
        color: var(--light-color);
        border: none;
        cursor: pointer;
        @media (max-width: 1440px){
             padding: 12px 22px; 
        }
        @media (max-width: 1240px){
             padding: 10px 18px; 
        }
        @media (max-width: 1100px){
             padding: 10px 14px; 
        }
        @media (max-width: 970px){
             padding: 16px 36px; 
        }
    }
}
.top-menu {
    &__list {
        display: flex;
        column-gap: 40px;
        @media (max-width: 1650px){
             column-gap: 20px; 
        }
        @media (max-width: 970px){
             flex-direction: column; 
             justify-content: center;
             align-items: center;
             gap: 60px;
        }
    }
    &__item {
        a{
            color: var(--light-color);
        }
    }
    &.menu--active{
        display: flex;
        transform: translateX(0%);
    }
    @media (max-width: 970px){
        position: fixed;    
        transform: translateX(300%);
        background: radial-gradient(1942.80% 462.32% at 26.53% -112.14%, #1C4D2D 0%, #00FF4E 100%);
        top: 0;
        right: 0;
        width: 450px;
        height: 100%;
        padding-top: 120px;
        flex-direction: column;
        font-size: 16px;
        gap: 80px;
        z-index: 10;
        transition: all 0.3s;
   }
   @media (max-width: 500px){
      width: 320px;
   }
}
@media (max-width: 970px){
    .burger { 
        @include burger;
        z-index: 11;
    } 
}
