// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1780px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --green: #008F32;
  --green2: #019A30;
  --lightgreen: #26EA41;
  --hvlightgreen: #4bff63;
  --darkgreen: #1C4D2D;
  --brown: #272727;
  --black: #000000;
}
