.equipment {
    &__container {
        display: flex;
        flex-direction: column;
    }
    &__subsection {
        overflow: hidden;
        @media (max-width: 600px){
            width: 570px;
            flex-shrink: 0; 
        }
    }
    &__wrapper {
    }
    &__item {
        box-sizing: border-box;
        text-align: center;
        height: 340px;
        border-radius: 20px;
        @media (max-width: 600px){
             height: 270px; 
        }
    }
    &__link {
        color: var(--light-color);
    }
    &__item-image {
        width: 100%;
        height: 270px;
        border-radius: 20px;
        object-fit: cover;
        margin-bottom: 30px;
        @media (max-width: 600px){
             height: 200px; 
        }
    }
    &__item-title {
        align-self: center;
        color: var(--black);
        text-align: center;
        font-size: 24px;
        font-weight: 500;
        line-height: 130%;
        span{
            display: block;
        }
        @media (max-width: 600px){
             font-size: 20px; 
        }
    }
}
