.footer {
    margin-top: 50px;
    &__container {
        border-radius: 20px 20px 0px 0px;
        padding: 60px 60px 30px;
        background: #112E1B;
        color: var(--light-color);
        @media (max-width: 970px){
             padding: 30px 20px 10px; 
        }
    }
    &__logo {
        @media (max-width: 970px){
             display: flex;
             justify-content: center; 
             margin-bottom: 40px;
        }
        img{
            width: 279px;
            height: 70px;
            @media (max-width: 970px){
                width: 195px;
                height: 49px;
            }
        }
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 70px;
        font-size: 24px;
        gap: 70px;
        @media (max-width: 1440px){
            font-size: 22px;
       }
       @media (max-width: 1240px){
            gap: 40px;
            font-size: 20px;
       }
       @media (max-width: 1100px){
            font-size: 18px;
        }
       @media (max-width: 970px){
            flex-direction: column;
            align-items: center;
        }
    }
    &__contacts {
        display: flex;
        flex-direction: column;
        span{
            font-weight: 600;
            margin-bottom: 40px;
        }
        @media (max-width: 970px){
             width: 90%; 
        }
    }
    &__link {
        overflow: hidden;
        padding: 20px 36px;
        &:not(:last-child) {
            margin-bottom: 30px;
            @media (max-width: 970px){
                margin-bottom: 20px; 
            }
        }
    }
    &__map {
        max-width: 880px;
        width: 100%;
        span{
            display: block;
            margin-bottom: 30px;
            font-weight: 400;
        }
    }
    &__map-content {
        width: 100%;
        height: 300px;
        border-radius: 20px;
        background: url('../img/footer-map.webp'), lightgray 50% / cover no-repeat;
    }
}
.copywright {
    font-size: 24px;
    font-weight: 300;
    text-align: center;
    @media (max-width: 970px){
         font-size: 11px; 
    }
}
