.partners {
    &__wrapper {
        display: grid;
        grid-template-columns: repeat(auto-fill, 400px);
        justify-content: center;
        gap: 24px 40px;
        @media (max-width: 1440px){
            grid-template-columns: repeat(auto-fill, 370px);
            gap: 24px; 
        }
        @media (max-width: 970px){
            grid-template-columns: repeat(auto-fill, 330px);
            gap: 20px; 
        }
    }
    &__item {
        justify-self: center;
    }
    &__item-image {
        height: 100px;
        border-radius: 10px;
        @media (max-width: 760px){
             height: 78px; 
        }
    }
}
.reviews-slider {
    display: flex;
    padding: 30px 0 40px;
    @media (max-width: 760px){
         padding-bottom: 0; 
    }
    &__container {
        position: relative;
        padding: 0 85px;
        overflow: hidden;
        @media (max-width: 1240px){
             padding: 0 70px; 
        }
        @media (max-width: 970px){
             padding: 0; 
        }
        @media (max-width: 760px){
             flex: 1 0 570px;
        }
    }
}
.reviews-slider__button-prev,
.reviews-slider__button-next {
    position: absolute;
    height: 40px;
    width: 40px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    @media (max-width: 970px){
         display: none; 
    }
}
.reviews-slider__button-prev {
    left: 0;
}
.reviews-slider__button-next {
    right: 0;
}
