.affecting-factors {
    &__title {
    }
    &__wrapper {
        display: flex;
        align-items: center;
        gap: 40px;
        padding: 40px;
        border-radius: 20px;
        background: var(--brown);
        @media (max-width: 1100px){
             gap: 0; 
        }
        @media (max-width: 500px){
             padding: 40px 20px; 
        }
    }
    &__image {
        width: 820px;
        height: 666px;
        border-radius: 20px;
        object-fit: cover;
        @media (max-width: 1100px){
             display: none; 
        }
    }
    &__list {
        width: 840px;
    }
    &__item {
        &:not(:last-child) {
            margin-bottom: 45px;
            @media (max-width: 1640px){
                 margin-bottom: 40px; 
            }
            @media (max-width: 1440px){
                 margin-bottom: 35px; 
            }
            @media (max-width: 760px){
                 margin-bottom: 30px; 
            }
        }
        a{
            margin-bottom: 25px;
            padding: 15px 30px;
            font-size: 20px;
            @media (max-width: 1640px){
                 font-size: 18px; 
                 margin-bottom: 20px;
            }
            @media (max-width: 760px){
                 font-size: 16px; 
                 margin-bottom: 15px;
                 padding: 10px 25px;
            }
        }
        span{
            display: block;
            color: var(--light-color);
            font-size: 20px;
            font-weight: 400;
            line-height: 130%; /* 26px */
            @media (max-width: 1440px){
                 font-size: 18px; 
            }
            @media (max-width: 760px){
                 font-size: 16px; 
            }
            @media (max-width: 500px){
                 font-size: 14px; 
            }
        }
    }
}
