.about-info {
    &__wrapper {
        display: flex;
        @media (max-width: 970px){
             flex-direction: column; 
        }
    }
    &__textbox {
        max-width: 1130px;
        margin-right: 70px;
        @media (max-width: 1440px){
            margin-right: 50px; 
        }
        @media (max-width: 1240px){
            margin-right: 20px; 
        }
        @media (max-width: 970px){
             order: 1;
             margin-right: 0;
             margin-top: 30px; 
        }
    }
    &__header {
        text-align: left;
        margin: 0;
        margin-bottom: 25px;
        span{
            color: var(--green);
        }
    }
    &__text {
        p{
            margin-bottom: 50px;
            font-size: 24px;
            font-weight: 500;
            line-height: 150%; /* 36px */
            @media (max-width: 1440px){
                 font-size: 22px; 
            }
            @media (max-width: 1240px){
                 font-size: 20px; 
                 margin-bottom: 40px;
            }
            @media (max-width: 1100px){
                 font-size: 18px; 
                 margin-bottom: 30px;
            }
            @media (max-width: 500px){
                 font-size: 14px; 
            }
        }
        strong{
            display: block;
            margin-bottom: 15px;
            font-size: 24px;
            font-weight: 600;
            line-height: 130%; /* 31.2px */
            @media (max-width: 1440px){
                 font-size: 22px; 
            }
            @media (max-width: 1240px){
                 font-size: 20px; 
            }
            @media (max-width: 1100px){
                 font-size: 18px; 
                 margin-bottom: 10px;
            }
            @media (max-width: 500px){
                 font-size: 16px; 
                 margin-bottom: 10px;
            }
        }
        small{
            font-size: 20px;
            font-weight: 500;
            line-height: 150%; /* 30px */
            @media (max-width: 1440px){
                 font-size: 18px; 
            }
            @media (max-width: 1240px){
                 font-size: 16px; 
            }
            @media (max-width: 500px){
                 font-size: 14px; 
            }
        }
    }
    &__img {
        flex-shrink: 0;
        width: 580px;
        @media (max-width: 1440px){
             width: 540px; 
        }
        @media (max-width: 1240px){
             width: 500px; 
        }
        @media (max-width: 1100px){
             width: 420px; 
        }
        @media (max-width: 970px){
             align-self: center; 
             flex-shrink: 1;
        }
        @media (max-width: 500px){
             width: 100%;              
        }
        img{
            border-radius: 20px;
            object-fit: cover;
        }
    }
}
