.extra-services-general, .included-renov {
    &__wrapper {
        display: grid;
        grid-template-columns: repeat(auto-fill, 23.5%);
        justify-content: center;
        gap: 20px;
        @media (max-width: 1440px){
            grid-template-columns: repeat(auto-fill, 32%);  
        }
        @media (max-width: 1100px){
            grid-template-columns: repeat(auto-fill, 48%);  
            gap: 15px;
        }
        @media (max-width: 760px){
            grid-template-columns: repeat(auto-fit, 100%);  
        }
    }
    &__item {
        &:nth-child(9){
            .dark-card-small__title{
                font-size: 24px;
            }
            .dark-card-small__add-info{
                small{
                    font-size: 24px;
                }
            }
            grid-column: span 4;
            @media (max-width: 1440px){
                grid-column: span 1;
                .dark-card-small__title{
                    font-size: 16px;
                }    
                .dark-card-small__add-info{
                    small{
                        font-size: 12px;
                    }
                }
            }
            @media (max-width: 1100px){
                grid-column: span 2;
                .dark-card-small__add-info{
                    font-size: 20px;
                    small{
                        font-size: 16px;
                    }
                }
            }
            @media (max-width: 760px){
                grid-column: span 1;
                .dark-card-small__title{
                    font-size: 14px;
                }    
                .dark-card-small__add-info{
                    small{
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
