.hero-main {
    padding: 508px 0 40px;
    height: 1002px;
    background-image: url('../img/main/hero-bg.webp');
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: cover;
    color: var(--light-color);
    font-size: 24px;  
    @media (max-width: 1440px){
        font-size: 22px; 
    }
    @media (max-width: 1240px){
        font-size: 20px; 
        height: 940px;
    }
    @media (max-width: 1100px){
        font-size: 18px; 
        height: 880px;
    }
    @media (max-width: 970px){
        font-size: 16px;
        padding-top: 370px;
        height: 770px;
    }
    @media (max-width: 500px){
        font-size: 13px;
        height: 700px;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 30px;
        @media (max-width: 1100px){
            margin-bottom: 20px; 
        }
        @media (max-width: 970px){
            flex-direction: column-reverse; 
            align-items: center;
        }
        @media (max-width: 500px){
             margin-bottom: 15px; 
        }
    }
    &__info {
        max-width: 860px;
        width: 100%;
        @media (max-width: 1440px){
             max-width: 760px; 
        }
        @media (max-width: 1240px){
             max-width: 600px; 
        }
        @media (max-width: 1100px){
             max-width: 500px; 
        }
        @media (max-width: 970px){
             max-width: 90%; 
        }
        @media (max-width: 760px){
             max-width: 100%; 
        }
    }
    &__header {
        font-size: 64px;
        font-weight: 800;
        margin-bottom: 40px;
        @media (max-width: 1440px){
            font-size: 54px; 
        }
        @media (max-width: 1240px){
            font-size: 44px; 
        }
        @media (max-width: 1100px){
            font-size: 34px; 
            margin-bottom: 20px;
        }
        @media (max-width: 500px){
            font-size: 24px; 
            margin-bottom: 14px;
        }
    }
    &__text {
        font-weight: 500;
        margin-bottom: 30px;
        @media (max-width: 1100px){
            margin-bottom: 20px;
        }
        @media (max-width: 970px){
            margin-bottom: 30px;
        }
    }
    &__btn {
        padding: 24px 40px;
        @media (max-width: 970px){
            display: block;
            padding: 16px 40px;
            margin: 0 auto; 
        }
        @media (max-width: 760px){
            width: 100%; 
        }
        @media (max-width: 500px){
            padding: 10px;
        }
    }
    &__tags {
        display: flex;
        flex-direction: column;
        gap: 20px;
        @media (max-width: 970px){
            flex-direction: row; 
            justify-content: space-around;
            margin-bottom: 20px;
            width: 90%;
        }
        @media (max-width: 760px){
            width: 100%;              
        }
        @media (max-width: 500px){
            margin-bottom: 12px;
            gap: 5px;
        }
    }
    &__tag {
        padding: 21px 48px;        
        @media (max-width: 970px){
            padding: 16px 35px;
            width: 100%;
        }
        @media (max-width: 760px){
            padding: 14px 10px;
            font-size: 12px;
        }
        @media (max-width: 500px){
            padding: 10px 5px;
            font-size: 10px;
        }
    }
}
