.clean-types {
    &__container {
        display: flex;
        flex-direction: column;
    }
    &__subsection {
        overflow: hidden;
        @media (max-width: 600px){
            width: 570px;
            flex-shrink: 0; 
        }
        &:not(:last-child) {
            margin-bottom: 60px;
            @media (max-width: 600px){
                margin-bottom: 40px; 
            }
        }
    }
    &__wrapper {
    }
    &__item {
        box-sizing: border-box;
        text-align: center;
        height: 340px;
        border-radius: 20px;
        padding: 25px;
        &--light {
            background: #019A30;
        }
        &--dark {
            background: #1C4D2D;
        }
        @media (max-width: 600px){
             height: 270px; 
        }
    }
    &__link {
        color: var(--light-color);
    }
    &__item-image {
        width: 100%;
        height: 200px;
        border-radius: 10px;
        object-fit: cover;
        margin-bottom: 15px;
        @media (max-width: 600px){
             height: 140px; 
        }
    }
    &__item-title {
        align-self: center;
        color: var(--light-color);
        text-align: center;
        font-size: 20px;
        font-weight: 600;
        line-height: 130%; /* 26px */
        span{
            display: block;
        }
        @media (max-width: 600px){
             font-size: 16px; 
        }
    }
}
.section-subtitle {
    color: #000;
    font-size: 24px;
    font-weight: 500;
    line-height: 130%; /* 31.2px */
    margin-bottom: 35px;
    @media (max-width: 970px){
        font-size: 20px;
        margin-bottom: 28px;  
    }
    @media (max-width: 500px){
        font-size: 16px;
        margin-bottom: 20px;  
    }
}
