html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  height: 100%;
  font-size: 20px;
  line-height: 1.2;
}

h1, h2, h3, h4, h5, h6 p {
  margin: 0;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  &:hover{
    text-decoration: underline;
  }
}

.site-container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}
.main{
  flex: 1 0 auto;
}
.footer{
  flex: 0 0 auto;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section {
  padding: 100px 0 40px;
  @media (max-width: 970px){
      padding: 75px 0 20px;
  }
  @media (max-width: 500px){
      padding: 50px 0 0;
  }
}
.section-title {
  margin: 0 auto;
  color: #000;
  margin-bottom: 108px;
  text-align: center;
  font-size: 32px;
  font-weight: 600;
  line-height: 130%; /* 41.6px */
  @media (max-width: 970px){
     font-size: 26px; 
     margin-bottom: 74px;
  }
  @media (max-width: 500px){
     font-size: 20px; 
     margin-bottom: 40px;
  }
}
.section-subtitle {
  color: #000;
  font-size: 24px;
  font-weight: 500;
  line-height: 130%; /* 31.2px */
  margin-bottom: 35px;
  @media (max-width: 970px){
      font-size: 20px;
      margin-bottom: 28px;  
  }
  @media (max-width: 500px){
      font-size: 16px;
      margin-bottom: 20px;  
  }
}

.btn-green {
  display: inline-block;
  color: var(--light-color);
  border: 1px solid var(--green);
  border-radius: 20px;
  background: var(--green);
  font-size: inherit;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  &:hover{
    background-color: var(--green2);
    color: var(--light-color);
  }
}
.btn-lightgreen {
  display: inline-block;
  color: #000;
  background: var(--lightgreen);
  font-size: inherit;
  font-weight: 700;
  border: 1px solid var(--lightgreen);
  border-radius: 12px;
  backdrop-filter: blur(8px);
  text-align: center;
  cursor: pointer;
  &:hover{
    background-color: var(--hvlightgreen);
    color: var(--black);
  }
}
.btn-black {
  display: inline-block;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--black);
  border-radius: 20px;
  background: #000;
  color: var(--light-color);
  cursor: pointer;
  &:hover{
    background-color: var(--light-color);
    color: var(--black);
    text-decoration: none;
  }
}
.footnotes {
  max-width: 600px;
  font-size: 12px;
  font-weight: 500;
  line-height: 130%; /* 10.4px */
  @media (max-width: 970px){
      margin: 0 auto;
  }
  @media (max-width: 760px){
       font-size: 8px; 
  }
}

