.main-form {
    &__wrapper {
        padding: 40px;
        display: flex;
        align-items: center;
        border-radius: 20px;
        background: var(--green);
        gap: 95px;
        @media (max-width: 1650px){
            gap: 50px; 
       }
        @media (max-width: 1440px){
            gap: 30px; 
       }
       @media (max-width: 1100px){
          padding: 30px;
       }
       @media (max-width: 970px){
          gap: 0;
       }
    }
    &__image {
        height: 500px;
        border-radius: 14px;
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        object-fit: cover;
        @media (max-width: 1100px){
             display: none; 
        }
    }
    &__content {
        height: 500px;
        max-width: 920px;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 37px 60px;
        justify-content: space-between;
        @media (max-width: 1650px){
            column-gap: 30px; 
       }
        @media (max-width: 1440px){
            column-gap: 20px; 
            height: 100%;
       }
        @media (max-width: 760px){
            grid-template-columns: repeat(1, 1fr); 
            gap: 30px;
       }
    }
    &__label {
        display: flex;
        flex-direction: column;
        span{
            margin-bottom: 20px;
            color: var(--light-color);
            font-size: 24px;
            line-height: 1;
            font-weight: 600;
            small{
                font-size: 16px;
                font-weight: 500;
            }
            @media (max-width: 1100px){
                 font-size: 20px; 
                 small{
                    font-size: 13px;
                 }
            }
            @media (max-width: 760px){
                 font-size: 16px; 
                 margin-bottom: 17px;
            }

        }
        input {
            padding: 15px;
            max-width: 430px;
            width: 100%;
            height: 60px;
            border-radius: 20px;
            border: none;
            outline: none;
            background: var(--light-color);
            @media (max-width: 760px){
                 height: 40px;
                 border-radius: 10px; 
                 max-width: 100%;
            }
        }
    }
    &__label--name {
        order: -4;
        @media (max-width: 760px){
             order: 1; 
        }
    }
    &__label--email {
        order: -2;
        @media (max-width: 760px){
             order: 1; 
        }
    }
    &__label--phone {
        order: 0;
        @media (max-width: 760px){
             order: 1; 
        }
    }
    &__label--theme {
        order: -3;
        @media (max-width: 760px){
             order: 1; 
        }
    }
    &__label--textarea {
        grid-row: 3 span;
        order: -1;
        @media (max-width: 760px){
             order: 1; 
        }
    }
    &__textarea {
        max-width: 430px;
        padding: 15px;
        width: 100%;
        height: 315px;
        border-radius: 20px;
        border: none;
        outline: none;
        background: var(--light-color);
        @media (max-width: 760px){
             height: 150px;
             border-radius: 10px; 
             max-width: 100%;
        }
    }
    &__btn {
        margin-top: 12px;
        align-self: flex-end;
        padding: 20px 40px;
        height: 65px;
        order: 1;
    }
}
