.results {
    &__container {
        display: flex;
        flex-direction: column;
    }
    &__subsection {
        display: flex;
        justify-content: center;
        overflow: hidden;
        text-align: center;
        width: 100%;
        @media (max-width: 500px){
            flex-shrink: 0;
            width: 540px;
        }
    }
    &__wrapper {
    }
    &__item {
        box-sizing: border-box;
    }
    &__link {
        display: flex;
        border-radius: 20px;
        overflow: hidden;
    }
    &__item-image {
        width: 100%;
        max-height: 360px;
        object-fit: cover;
        @media (max-width: 500px){
             height: 162px; 
        }
    }
}
