.accordion {
    list-style: none;
    padding: 0;
    counter-reset: accounter;
    &__item {
        &:not(:last-child) {
            margin-bottom: 15px;
        }
        &.open .accordion__text {
            opacity: 1;
            margin-top: 15px;
            margin-bottom: 15px;
        }
    }
    &__title {
        background: #272727;
        padding: 25px 30px;
        border-radius: 10px;
        color: var(--light-color);
        width: 100%;
        height: 80px;
        text-align: left;
        font-size: 24px;
        font-weight: 500;
        border: none;
        cursor: pointer;
        &::before{
            counter-increment: accounter;
            content: counter(accounter)'. ';
            color: var(--light-color);
        }
        @media (max-width: 1100px){
             font-size: 20px; 
        }
        @media (max-width: 970px){
             font-size: 18px; 
             padding: 20px;
             height: 70px;
        }
        @media (max-width: 760px){
             font-size: 16px; 
        }
        @media (max-width: 500px){
             font-size: 13px; 
             padding: 15px 12px;
             height: 60px;
        }
    }
    &__text {
        max-height: 0;
        opacity: 0;
        overflow: hidden;
        padding: 0 20px 0 30px;
        margin-left: 30px;
        border-left: 3px solid var(--lightgreen);
        will-change: max-height;
        transition: all 0.3s ease-out;
        box-sizing: content-box;
        font-size: 20px;
        font-weight: 400;
        line-height: 130%; /* 20.8px */
        @media (max-width: 970px){
            padding-left: 20px;
            margin-left: 20px;
            font-size: 18px; 
        }
        @media (max-width: 760px){
            font-size: 16px; 
        }
        @media (max-width: 500px){
            font-size: 13px; 
            margin-left: 15px;
            padding-left: 15px;
        }
        
    }
}
