.price-policy, .price-range {
    &__header {
        display: block;
    }
    &__container {
        overflow: hidden;
        margin-bottom: 40px;
        @media (max-width: 600px){
            flex-shrink: 0; 
            width: 570px;
        }
    }
    &__wrapper {
    }
    &__item {
        &.price-card{
            padding: 63px 20px 28px;
        }
        .price-card__area{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 97px;
            font-size: 20px;
            font-weight: 700;
            span{
                font-size: 24px;
            }
        }
        .price-card__price{
            display: inline;
            font-size: 20px;
            font-weight: 500;
        }
    }
}

