.hero-basic {
    padding: 180px 0 20px;
    height: 100%;
    @media (max-width: 970px){
         padding: 150px 0 0; 
    }
    @media (max-width: 760px){
         padding: 120px 0 0; 
    }
    @media (max-width: 500px){
         padding: 90px 0 0; 
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: start;
        @media (max-width: 970px){
            flex-direction: column;
            justify-content: start;
            align-items: center;  
        }
    }
    &__info {
        display: flex;
        flex-direction: column;
        max-width: 730px;
        min-height: 732px;
        margin-right: 15px;
        @media (max-width: 1440px){
            max-width: 550px; 
            min-height: 662px;
        }
        @media (max-width: 1240px){
            max-width: 380px; 
            min-height: 600px;
        }
        @media (max-width: 970px){
            max-width: 90%; 
            min-height: 100%;
            order: 1; 
        }
        @media (max-width: 760px){
            max-width: 100%; 
        }
    }
    &__header {
        display: block;
        margin-bottom: 30px;
        font-family: "Inter", sans-serif;
        font-size: 64px;
        font-weight: 700;
        line-height: 140%; /* 89.615px */
        span{
            color: var(--green);
        }
        @media (max-width: 1440px){
             font-size: 48px; 
        }
        @media (max-width: 1240px){
             font-size: 34px; 
        }
        @media (max-width: 760px){
            margin-bottom: 30px;
            font-size: 28px; 
        }
        @media (max-width: 500px){
            margin-bottom: 20px;
            font-size: 24px; 
        }
    }
    &__text {
        display: block;
        color: var(--black);
        font-family: "Inter", sans-serif;
        font-size: 24px;
        font-weight: 400;
        line-height: 140%; /* 33.605px */
        @media (max-width: 1440px){
            font-size: 22px;
        }
        @media (max-width: 1240px){
            font-size: 20px;
        }
        @media (max-width: 500px){
            font-size: 16px;
        }
    }
    &__btn {
        display: block;
        margin-top: auto;
        margin-bottom: 0;
        padding: 24px 40px;
        width: 370px;
        @media (max-width: 970px){
            width: 100%;  
        }
        @media (max-width: 760px){
            padding: 20px; 
        }
        @media (max-width: 500px){
            padding: 15px; 
        }
    }
    &__imgs {
        display: flex;
        align-items: start;
        picture:nth-child(1){
            margin-right: 20px;
            img{
                width: 430px;
                height: 732px;
            }
        }
        picture:nth-child(2){
            img{
                width: 280px;
                height: 640px;
            }
        }
        @media (max-width: 1440px){
            picture:nth-child(1){
                img{
                    width: 390px;
                    height: 662px;
                }
            }
            picture:nth-child(2){
                img{
                    width: 250px;
                    height: 580px;
                }
            }              
        }
        @media (max-width: 1240px){
            picture:nth-child(1){
                margin-right: 15px;
                img{
                    width: 350px;
                    height: 600px;
                }
            }
            picture:nth-child(2){
                img{
                    width: 225px;
                    height: 520px;
                }
            }              
        }
        @media (max-width: 970px){
            margin-bottom: 36px;  
        }
        @media (max-width: 760px){
            picture:nth-child(1){
                margin-right: 10px;
                img{
                    width: 350px;
                    height: 100%;
                }
            }
            picture:nth-child(2){
                img{
                    width: 225px;
                    height: 100%;
                }
            }              
        }
    }
    &__image {
        border-radius: 20px;
        object-fit: cover;
        // &:nth-child(1){
        //     margin-right: 20px;
        //     img{
        //         width: 430px;
        //         height: 732px;
        //     }
        // }
        // &:nth-child(2){
        //     img{
        //         width: 280px;
        //         height: 640px;
        //     }
        // }
    }
}
