.hero-about {
    margin-top: 100px;
    padding: 180px 0 80px;
    min-height: 1002px;
    color: var(--light-color);
    font-size: 24px;  
    background-color: rgba(#000000, 0.3);
    position: relative;
    &::after{
        content:'';
        position: absolute;
        left: 0;
        bottom: 0;
        background-image: url('../img/about/hero-bg.webp');
        background-repeat: no-repeat;
        background-position: center bottom;
        background-size: cover;
        height: 100%;
        width: 100%;
        z-index: -1;
    }
    @media (max-width: 1440px){
        font-size: 22px; 
        margin-top: 90px;
    }
    @media (max-width: 1240px){
        font-size: 20px; 
        height: 940px;
        margin-top: 80px;
    }
    @media (max-width: 1100px){
        font-size: 18px; 
        height: 880px;
        margin-top: 70px;
    }
    @media (max-width: 970px){
        font-size: 16px;
        height: 770px;
        margin-top: 60px;
    }
    @media (max-width: 500px){
        font-size: 13px;
        height: 700px;
        margin-top: 50px;
    }
    &__info{
        margin-top: 640px;
        @media (max-width: 1240px){
             margin-top: 590px; 
        }
        @media (max-width: 970px){
             margin-top: 500px; 
        }
        @media (max-width: 500px){
             margin-top: 450px; 
        }
    }
    &__header {
        font-size: 64px;
        font-weight: 800;
        margin-bottom: 30px;
        @media (max-width: 1440px){
            font-size: 54px; 
        }
        @media (max-width: 1240px){
            font-size: 44px; 
        }
        @media (max-width: 1100px){
            font-size: 34px; 
            margin-bottom: 20px;
        }
        @media (max-width: 500px){
            font-size: 24px; 
            margin-bottom: 14px;
        }
    }
}
