.included-general, 
.business-services {
    &__wrapper {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        justify-content: center;
        gap: 20px;
        @media (max-width: 1100px){
            gap: 15px;
        }
    }
    &__item {
        grid-column: span 3;
        @media (max-width: 1440px){
            grid-column: span 4;  
            &:nth-child(7), &:nth-child(8){
                grid-column: span 6;
            }
        }
        @media (max-width: 1100px){
            grid-column: span 6;  
        }
        @media (max-width: 760px){
            grid-column: span 12;  
            &:nth-child(7), &:nth-child(8){
                grid-column: span 12;
            }
        }
    }
}